:root {
  --main-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav {
  margin-right: 50px;
}

.logo {
  margin-left: 50px;
}

.logo img {
  vertical-align: middle;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  margin-right: 20px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: var(--main-color);
}

.nav__item:last-child .nav__link {
  margin-right: 0;
}

.nav__link.is-active {
  color: var(--main-color);
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

